
routesConfig.$inject = ["$stateProvider", "$urlRouterProvider", "$locationProvider"];
helloController.$inject = ["$scope", "$http"];angular
  .module('app', ['ui.router', 'ui.bootstrap']);

angular
  .module('app')
  .directive('newsItem', function () {
    return {
      restrict: 'E',
      scope: {
        itemData: '=data'
      },
      templateUrl: 'app/newsitem/newsitem.html'
    };
  }
);

angular
  .module('app')
  .component('app', {
    templateUrl: 'app/hello.html',
    controller: helloController
  });

function helloController($scope, $http) {
  var unObjet = {
    title: 'Je suis un titre',
    text: 'Ce texte pourrait être plus long. Ce texte pourrait être plus long.Ce texte pourrait être plus long.Ce texte pourrait être plus long.Ce texte pourrait être plus long.Ce texte pourrait être plus long.Ce texte pourrait être plus long.',
    date: '2017-03-',
    infos: "pas d'infos pour le moment"
  };
  $scope.tags = [];
  $http
    .get("data/taglist.json")
    .then(function (result) {
      $scope.tags = result.data;
    });
  $scope.limitExpand = 5;
  $scope.limit = $scope.limitExpand;
  var news = [];
  for (var i = 0; i < 20; i++) {
    var newObject = angular.copy(unObjet);
    newObject.title += " " + i;
    newObject.date += i;
    news.push(newObject);
  }
  $scope.selectedNews = performSeach('', news);
  $scope.$watch('searchText', function (search) {
    $scope.selectedNews = performSeach(search, news);
  });
}

function performSeach(search, news) {
  for (var i = 0; i < news.length; i++) {
    news[i].relevance = computeRelevance(search, news[i]);
  }
  // only news item with non 0 relevance
  var selected = news.filter(function (element) {
    return element.relevance > 0;
  });
  // sort by relevance and time
  selected = selected.sort(function (a, b) {
    var comp = b.relevance - a.relevance;
    if (comp === 0) {
      if (a.date > b.date) {
        comp = -1;
      } else if (a.date < b.date) {
        comp = 1;
      }
    }
    return comp;
  });
  return selected;
}

function computeRelevance(search, newsitem) {
  var tags = newsitem.tags | [];
  tags[0] = 0;
  var relevance = 0;
  if (search) {
    // compute
    relevance = 100 * Math.random().toPrecision(2);
  } else {
    relevance = 1;
  }
  return relevance;
}

angular.module("app").run(["$templateCache", function($templateCache) {$templateCache.put("app/hello.html","<div class=\"container-fluid\">\n    <div class=\"col-md-3\"></div>\n    <div class=\"col-md-6\">\n      <div class=\"row\">\n        <div class=\"page-header\">\n        <h1>Newsletter technique de Quantmetry<br><small>I\'m reading blog posts so you don\'t have to</small></h1>\n        <div class=\"input-group input-group-lg\">\n          <span class=\"input-group-addon\" id=\"sizing-addon1\">Recherche</span>\n          <input type=\"text\" class=\"form-control\" placeholder=\"search for words\" aria-describedby=\"sizing-addon1\" ng-model=\"searchText\" uib-typeahead=\"tag for tag in tags  | filter:$viewValue | limitTo:10\" typeahead-editable=\"true\">\n          <!-- ng-model-options=\"{ debounce: 1000 }\" typeahead-wait-ms=\"0\" -->\n          <!-- <input type=\"submit\" id=\"submit\" value=\"Submit\" /> -->\n          <!-- <ng-submit\n            ng-submit=\"performSearch(test)\">\n\n          </ng-submit> -->\n        </div>\n      </div>\n      </div>\n\n        <div class=\"row\">\n          <div ng-repeat=\"item in selectedNews | limitTo: limit as results\">\n            <news-item data=\"item\"></news-item>\n          </div>\n        </div>\n        <div class=\"row\">\n          <button type=\"button\" class=\"btn btn-primary btn-block\" ng-hide=\"results.length === selectedNews.length\" ng-click=\"limit = limit + limitExpand\">\n                Show {{limitExpand}} More ({{selectedNews.length - results.length}} remaining)\n          </button>\n        </div>\n    </div>\n    <div class=\"col-md-3\"></div>\n</div>\n\n<div class=\"footer\">\n  <p>Copyright (c) 2017</p>\n</div>\n");
$templateCache.put("app/newsitem/newsitem.html","<div class=\"panel panel-default\">\n  <div class=\"panel-heading\">\n    <h3 class=\"panel-title\">{{itemData.title}}</h3>\n    <p> {{itemData.date}}   {{itemData.relevance}} </p>\n  </div>\n  <div class=\"panel-body\">\n    {{itemData.text}}\n  </div>\n</div>\n");}]);
angular
  .module('app')
  .config(routesConfig);

/** @ngInject */
function routesConfig($stateProvider, $urlRouterProvider, $locationProvider) {
  $locationProvider.html5Mode(true).hashPrefix('!');
  $urlRouterProvider.otherwise('/');

  $stateProvider
    .state('app', {
      url: '/',
      component: 'app'
    });
}
